import React, { useEffect } from 'react';
import OrderingPay from './OrderingPay';

const OrderingPayContainer = ({
  payment_methods = [],
  total_cost,
  balance,
  values,
  role,

  setValues,
}) => {
  const setActiveVariantPayments = () => { 
      setValues({
        ...values,
        'payment_methods': balance >= total_cost? 3 : 1
      })
  };

  useEffect(() => {
    const timerSetTimeout = setTimeout(() => {
      setActiveVariantPayments()
      clearTimeout(timerSetTimeout)
    }, 1000)
  }, [])

  return (
    <OrderingPay
      payment_methods={payment_methods}
      total_cost={total_cost}
      balance={balance}
      values={values}
      role={role}

      // setActiveVariantPayments = { setActiveVariantPayments }
      setValues={setValues}
    />
  );
};

export default OrderingPayContainer;
